
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { ICustomerGroupForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import CustomerGroupForm from '@/views/client/customer-group/forms/CustomerGroupForm.vue'

export interface IData {
  model: ICustomerGroupForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: ICustomerGroupForm) => {
      return store.dispatch('customerGroup/createForm', dataForm)
    }) as ResponseFetching<Promise<ICustomerGroupForm>>

    const form = ref()

    return {
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    CustomerGroupForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        sortOrder: 0,
        description: '',
        name: 'New group'
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'customer-group' })
    },

    async handleSubmit () {
      if (this.form && await this.form.validate()) {
        const data: ICustomerGroupForm = await this.saveForm(this.model)
        if (typeof data !== 'undefined') {
          this.model = data
        }

        if (this.model?.id) {
          this.$router.push({ name: 'customer-group-update', params: { id: this.model.id } })
        }
      }
    }
  }
})
